.sign-up-page{
    width: 100%;
    height: 100vh;
    .sign-up-page-container{
        position:absolute; 
        top:40%; 
        left:50%; 
        transform:translate(-50%, -50%); 
    }
    .title{
      line-height: 1.6;
      letter-spacing: 1px;
      font-size: 2.4rem;
      .color{
        color: var(--clr3);
      }
      &:last-of-type{
        margin-bottom: 1.2rem;
      }
    }
    .input{
        padding:6px;
        border-radius: 5px;
        width:210px;
        border: none;
    }
}